"use client"

import { useCallback, useEffect, useRef } from 'react'

type SoundType = 'correct' | 'incorrect' | 'levelComplete' | 'drop'

const AUDIO_URLS = {
  correct: 'https://assets.mixkit.co/active_storage/sfx/2000/2000-preview.mp3',
  incorrect: 'https://assets.mixkit.co/active_storage/sfx/2003/2003-preview.mp3',
  levelComplete: 'https://assets.mixkit.co/active_storage/sfx/1435/1435-preview.mp3',
  drop: 'https://assets.mixkit.co/active_storage/sfx/2571/2571-preview.mp3'
}

export function useGameAudio() {
  const audioRefs = useRef<{ [key in SoundType]?: HTMLAudioElement }>({})

  useEffect(() => {
    // Preload audio files
    Object.entries(AUDIO_URLS).forEach(([key, url]) => {
      const audio = new Audio(url)
      audio.preload = 'auto'
      audioRefs.current[key as SoundType] = audio
    })

    // Cleanup
    return () => {
      Object.values(audioRefs.current).forEach(audio => {
        if (audio) {
          audio.pause()
          audio.src = ''
        }
      })
    }
  }, [])

  const playSound = useCallback((type: SoundType) => {
    const audio = audioRefs.current[type]
    if (audio) {
      audio.currentTime = 0
      audio.play().catch(error => {
        console.log('Audio playback failed:', error)
      })
    }
  }, [])

  return { playSound }
}
