"use client"

import { useDrop } from 'react-dnd'
import { cn } from "@/lib/utils"
import { useRef } from 'react'

interface LetterDropZoneProps {
  onDrop: (letter: string) => void
  className?: string
  children?: React.ReactNode
}

export function LetterDropZone({ onDrop, className, children }: LetterDropZoneProps) {
  const ref = useRef<HTMLDivElement>(null)
  const [{ isOver }, drop] = useDrop(() => ({
    accept: 'letter',
    drop: (item: { letter: string }) => {
      console.log('LetterDropZone received letter:', item.letter)
      onDrop(item.letter.toUpperCase())
      return undefined
    },
    collect: (monitor) => ({
      isOver: monitor.isOver(),
    }),
  }), [onDrop])

  // Combine refs
  const dropRef = (element: HTMLDivElement | null) => {
    ref.current = element
    drop(element)
  }

  return (
    <div
      ref={dropRef}
      className={cn(
        'h-20 w-20 border-4 rounded-lg flex items-center justify-center',
        isOver ? 'border-purple-500 bg-purple-100' : 'border-dashed border-gray-400',
        className
      )}
    >
      {children}
    </div>
  )
}
