"use client"

import { useState, useCallback } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import Image from "next/image"
import { DndProvider } from 'react-dnd'
import { HTML5Backend } from 'react-dnd-html5-backend'
import { motion, AnimatePresence } from "framer-motion"
import { useGameAudio } from "@/hooks/useGameAudio"
import { SyllableDropZone } from "./SyllableDropZone"
import { DraggableLetter } from "./DraggableLetter"
import { CelebrationModal } from "./CelebrationModal"

interface Level2Props {
  onBack: () => void
  onComplete?: () => void
}

interface Exercise {
  consonant: string
  vowel: string
  syllable: string
  word: string
  image: string
  options: string[]
  audioUrl?: string
}

const exercises: Exercise[] = [
  {
    consonant: "D",
    vowel: "E",
    syllable: "DE",
    word: "Dedo",
    image: "https://images.pexels.com/photos/4066041/pexels-photo-4066041.jpeg",
    options: ["Dedo", "Mesa", "Tela"],
    audioUrl: "https://assets.mixkit.co/active_storage/sfx/2148/2148-preview.mp3"
  },
  {
    consonant: "G",
    vowel: "A",
    syllable: "GA",
    word: "Galleta",
    image: "https://images.pexels.com/photos/230325/pexels-photo-230325.jpeg",
    options: ["Galleta", "Pato", "Rato"],
    audioUrl: "https://assets.mixkit.co/active_storage/sfx/2149/2149-preview.mp3"
  },
  {
    consonant: "L",
    vowel: "E",
    syllable: "LE",
    word: "Leche",
    image: "https://images.pexels.com/photos/248412/pexels-photo-248412.jpeg",
    options: ["Leche", "Mesa", "Pera"],
    audioUrl: "https://assets.mixkit.co/active_storage/sfx/2150/2150-preview.mp3"
  }
]

export default function Level2({ onBack, onComplete }: Level2Props) {
  const [currentExerciseIndex, setCurrentExerciseIndex] = useState(0)
  const [consonantPlaced, setConsonantPlaced] = useState(false)
  const [vowelPlaced, setVowelPlaced] = useState(false)
  const [showWordSelection, setShowWordSelection] = useState(false)
  const [selectedWord, setSelectedWord] = useState<string | null>(null)
  const [isCorrect, setIsCorrect] = useState<boolean | null>(null)
  const [showCelebration, setShowCelebration] = useState(false)
  const [isLevelComplete, setIsLevelComplete] = useState(false)
  const [score, setScore] = useState(0)
  const { playSound } = useGameAudio()

  const currentExercise = exercises[currentExerciseIndex]

  const handleLetterDrop = useCallback((letter: string, type: 'consonant' | 'vowel') => {
    console.log(`Dropped ${type}: ${letter}`)
    console.log(`Expected ${type}: ${type === 'consonant' ? currentExercise.consonant : currentExercise.vowel}`)
    
    const isCorrectLetter = letter === (type === 'consonant' ? currentExercise.consonant : currentExercise.vowel)
    
    if (isCorrectLetter) {
      if (type === 'consonant') {
        setConsonantPlaced(true)
      } else {
        setVowelPlaced(true)
      }

      playSound('correct')

      // If both letters are placed correctly
      if ((type === 'consonant' && vowelPlaced) || (type === 'vowel' && consonantPlaced)) {
        setTimeout(() => {
          // Play syllable sound if available
          if (currentExercise.audioUrl) {
            const audio = new Audio(currentExercise.audioUrl)
            audio.play().catch(console.error)
          }
          setShowWordSelection(true)
        }, 1000)
      }
    } else {
      playSound('incorrect')
    }
  }, [currentExercise, vowelPlaced, consonantPlaced, playSound])

  const handleWordSelection = useCallback((word: string) => {
    setSelectedWord(word)
    const correct = word === currentExercise.word
    setIsCorrect(correct)

    if (correct) {
      playSound('correct')
      setScore(prev => prev + 1)
      setShowCelebration(true)

      if (currentExerciseIndex === exercises.length - 1) {
        setTimeout(() => {
          playSound('levelComplete')
          setIsLevelComplete(true)
        }, 1000)
      }
    } else {
      playSound('incorrect')
    }
  }, [currentExercise, currentExerciseIndex, playSound])

  const handleContinue = useCallback(() => {
    setShowCelebration(false)
    setSelectedWord(null)
    setIsCorrect(null)
    setConsonantPlaced(false)
    setVowelPlaced(false)
    setShowWordSelection(false)
    
    if (currentExerciseIndex < exercises.length - 1) {
      setCurrentExerciseIndex(prev => prev + 1)
    }
  }, [currentExerciseIndex])

  return (
    <DndProvider backend={HTML5Backend}>
      <div className="max-w-4xl mx-auto p-8">
        <div className="flex justify-between items-center mb-8">
          <h1 className="text-3xl font-bold text-purple-800">
            La Fábrica de Sílabas
          </h1>
          <div className="text-xl font-semibold text-purple-600">
            Puntos: {score} / {exercises.length}
          </div>
        </div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          key={currentExercise.syllable}
        >
          <Card className="p-8 bg-white/90 backdrop-blur shadow-xl">
            {!showWordSelection ? (
              <div className="space-y-8">
                <h2 className="text-2xl font-bold text-center mb-8">
                  ¡Forma una sílaba!
                </h2>
                
                <div className="flex justify-center gap-8">
                  <SyllableDropZone
                    type="consonant"
                    onDrop={(letter) => handleLetterDrop(letter, 'consonant')}
                    isPlaced={consonantPlaced}
                    letter={consonantPlaced ? currentExercise.consonant : null}
                  />
                  <SyllableDropZone
                    type="vowel"
                    onDrop={(letter) => handleLetterDrop(letter, 'vowel')}
                    isPlaced={vowelPlaced}
                    letter={vowelPlaced ? currentExercise.vowel : null}
                  />
                </div>

                <div className="grid grid-cols-2 gap-4 mt-8 justify-items-center">
                  <DraggableLetter
                    letter={currentExercise.consonant}
                    isCorrect={consonantPlaced ? true : null}
                  />
                  <DraggableLetter
                    letter={currentExercise.vowel}
                    isCorrect={vowelPlaced ? true : null}
                  />
                </div>
              </div>
            ) : (
              <div className="space-y-8">
                <h2 className="text-2xl font-bold text-center mb-8">
                  ¿Qué palabra comienza con "{currentExercise.syllable}"?
                </h2>

                <div className="relative h-64 mb-8 rounded-lg overflow-hidden">
                  <Image
                    src={currentExercise.image}
                    alt={currentExercise.word}
                    fill
                    className="object-cover"
                    sizes="(max-width: 768px) 100vw, 768px"
                    priority
                  />
                </div>

                <div className="grid grid-cols-3 gap-4">
                  {currentExercise.options.map((word) => (
                    <Button
                      key={word}
                      onClick={() => handleWordSelection(word)}
                      className={`
                        text-xl font-bold p-6
                        ${selectedWord === word
                          ? isCorrect
                            ? 'bg-green-500 hover:bg-green-600'
                            : 'bg-red-500 hover:bg-red-600'
                          : 'bg-purple-500 hover:bg-purple-600'
                        }
                      `}
                    >
                      {word}
                    </Button>
                  ))}
                </div>
              </div>
            )}
          </Card>
        </motion.div>

        <AnimatePresence mode="wait">
          {showCelebration && !isLevelComplete && (
            <CelebrationModal
              title="¡Excelente!"
              message="¡Has formado la sílaba correctamente!"
              onClose={handleContinue}
            />
          )}

          {isLevelComplete && (
            <CelebrationModal
              title="¡Nivel Completado!"
              message={`¡Felicitaciones! Has completado la Fábrica de Sílabas con ${score} puntos.`}
              onClose={onBack}
              onNext={onComplete}
              isLevelComplete={true}
            />
          )}
        </AnimatePresence>

        <div className="mt-8 text-center">
          <Button
            onClick={onBack}
            className="bg-gray-500 hover:bg-gray-600 text-white"
          >
            Volver al Menú
          </Button>
        </div>
      </div>
    </DndProvider>
  )
}
