"use client"

import { useDrag } from 'react-dnd'
import { Button } from "@/components/ui/button"
import { useRef } from 'react'

interface DraggableLetterProps {
  letter: string
  isCorrect?: boolean | null
}

export function DraggableLetter({ letter, isCorrect }: DraggableLetterProps) {
  const ref = useRef<HTMLDivElement>(null)
  const [{ isDragging }, drag] = useDrag(() => ({
    type: 'letter',
    item: { letter: letter.toUpperCase() },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  }))

  drag(ref)

  return (
    <div
      ref={ref}
      style={{
        opacity: isDragging ? 0.5 : 1,
        cursor: 'move',
      }}
    >
      <Button
        className={`
          text-2xl font-bold h-16 w-16
          ${isCorrect === true ? 'bg-green-500 hover:bg-green-600' : 
            isCorrect === false ? 'bg-red-500 hover:bg-red-600' :
            'bg-purple-500 hover:bg-purple-600'}
          transition-colors duration-200
          transform hover:scale-105
        `}
      >
        {letter}
      </Button>
    </div>
  )
}
