"use client"

import { useDrop } from 'react-dnd'
import { cn } from "@/lib/utils"
import { motion, AnimatePresence } from "framer-motion"
import { useRef } from 'react'

interface WordDropZoneProps {
  position: number
  onDrop: (syllable: string, position: number) => void
  placedSyllable: string | undefined
  className?: string
}

export function WordDropZone({ 
  position, 
  onDrop, 
  placedSyllable, 
  className 
}: WordDropZoneProps) {
  const ref = useRef<HTMLDivElement>(null)
  const [{ isOver }, drop] = useDrop(() => ({
    accept: 'syllable',
    drop: (item: { syllable: string }) => {
      console.log(`WordDropZone received syllable:`, item.syllable)
      onDrop(item.syllable, position)
      return undefined
    },
    collect: (monitor) => ({
      isOver: monitor.isOver(),
    }),
  }), [onDrop, position])

  // Combine refs
  const dropRef = (element: HTMLDivElement | null) => {
    ref.current = element
    drop(element)
  }

  return (
    <div
      ref={dropRef}
      className={cn(
        'h-24 w-24 border-4 rounded-lg flex items-center justify-center',
        isOver ? 'border-purple-500 bg-purple-100' : 'border-dashed border-gray-400',
        placedSyllable ? 'border-solid border-green-500 bg-green-100' : '',
        className
      )}
    >
      <AnimatePresence mode="wait">
        {placedSyllable ? (
          <motion.span
            initial={{ scale: 0 }}
            animate={{ scale: 1 }}
            exit={{ scale: 0 }}
            className="text-3xl font-bold text-gray-800"
          >
            {placedSyllable}
          </motion.span>
        ) : (
          <motion.span
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            className="text-lg text-gray-500"
          >
            {position + 1}
          </motion.span>
        )}
      </AnimatePresence>
    </div>
  )
}
