"use client"

import { useDrop } from 'react-dnd'
import { cn } from "@/lib/utils"
import { motion, AnimatePresence } from "framer-motion"
import { useRef } from 'react'

interface SyllableDropZoneProps {
  type: 'consonant' | 'vowel'
  onDrop: (letter: string) => void
  isPlaced: boolean
  letter: string | null
  className?: string
}

export function SyllableDropZone({ 
  type, 
  onDrop, 
  isPlaced, 
  letter, 
  className 
}: SyllableDropZoneProps) {
  const ref = useRef<HTMLDivElement>(null)
  const [{ isOver }, drop] = useDrop(() => ({
    accept: 'letter',
    drop: (item: { letter: string }) => {
      console.log(`SyllableDropZone (${type}) received letter:`, item.letter)
      onDrop(item.letter.toUpperCase())
      return undefined
    },
    collect: (monitor) => ({
      isOver: monitor.isOver(),
    }),
  }), [onDrop, type])

  // Combine refs
  const dropRef = (element: HTMLDivElement | null) => {
    ref.current = element
    drop(element)
  }

  return (
    <div
      ref={dropRef}
      className={cn(
        'h-24 w-24 border-4 rounded-lg flex items-center justify-center',
        isOver ? 'border-purple-500 bg-purple-100' : 'border-dashed border-gray-400',
        isPlaced ? 'border-solid border-green-500 bg-green-100' : '',
        className
      )}
    >
      <AnimatePresence mode="wait">
        {letter && (
          <motion.span
            initial={{ scale: 0 }}
            animate={{ scale: 1 }}
            exit={{ scale: 0 }}
            className="text-4xl font-bold text-gray-800"
          >
            {letter}
          </motion.span>
        )}
        {!letter && (
          <motion.span
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            className="text-lg text-gray-500"
          >
            {type === 'consonant' ? 'Consonante' : 'Vocal'}
          </motion.span>
        )}
      </AnimatePresence>
    </div>
  )
}
