"use client"

import { useState, useEffect, useCallback } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import Image from "next/image"
import { DndProvider } from 'react-dnd'
import { HTML5Backend } from 'react-dnd-html5-backend'
import { DraggableLetter } from "./DraggableLetter"
import { LetterDropZone } from "./LetterDropZone"
import { CelebrationModal } from "./CelebrationModal"
import { motion, AnimatePresence } from "framer-motion"
import { useGameAudio } from "@/hooks/useGameAudio"

interface Level1Props {
  onBack: () => void
  onComplete?: () => void
}

interface Word {
  word: string
  image: string
  initialSound: string
  options: string[]
  audioUrl?: string
}

const words: Word[] = [
  {
    word: "Mano",
    image: "https://images.pexels.com/photos/1181686/pexels-photo-1181686.jpeg",
    initialSound: "M",
    options: ["M", "P", "L"],
    audioUrl: "https://assets.mixkit.co/active_storage/sfx/2148/2148-preview.mp3"
  },
  {
    word: "Uña",
    image: "https://images.pexels.com/photos/939836/pexels-photo-939836.jpeg",
    initialSound: "U",
    options: ["U", "O", "A"],
    audioUrl: "https://assets.mixkit.co/active_storage/sfx/2149/2149-preview.mp3"
  },
  {
    word: "Pie",
    image: "https://images.pexels.com/photos/1476218/pexels-photo-1476218.jpeg",
    initialSound: "P",
    options: ["P", "B", "D"],
    audioUrl: "https://assets.mixkit.co/active_storage/sfx/2150/2150-preview.mp3"
  }
]

export default function Level1({ onBack, onComplete }: Level1Props) {
  const [currentWordIndex, setCurrentWordIndex] = useState(0)
  const [selectedLetter, setSelectedLetter] = useState<string | null>(null)
  const [isCorrect, setIsCorrect] = useState<boolean | null>(null)
  const [showCelebration, setShowCelebration] = useState(false)
  const [isLevelComplete, setIsLevelComplete] = useState(false)
  const [score, setScore] = useState(0)
  const { playSound } = useGameAudio()

  const currentWord = words[currentWordIndex]

  const handleDrop = useCallback((letter: string) => {
    // Both letters should be uppercase since DraggableLetter and LetterDropZone handle uppercase
    const droppedLetter = letter.toUpperCase()
    const expectedLetter = currentWord.initialSound.toUpperCase()
    
    console.log('Current word:', currentWord.word)
    console.log('Dropped letter:', droppedLetter)
    console.log('Expected letter:', expectedLetter)
    
    setSelectedLetter(letter)
    const correct = droppedLetter === expectedLetter
    setIsCorrect(correct)

    if (correct) {
      playSound('correct')
      setScore(prev => prev + 1)
      setShowCelebration(true)
      
      if (currentWordIndex === words.length - 1) {
        setTimeout(() => {
          playSound('levelComplete')
          setIsLevelComplete(true)
        }, 1000)
      }
    } else {
      playSound('incorrect')
    }
  }, [currentWord, currentWordIndex, playSound])

  const handleContinue = useCallback(() => {
    console.log('Continue clicked, current index:', currentWordIndex)
    setShowCelebration(false)
    setSelectedLetter(null)
    setIsCorrect(null)
    
    if (currentWordIndex < words.length - 1) {
      setCurrentWordIndex(prev => {
        console.log('Moving to next word, from index:', prev, 'to:', prev + 1)
        return prev + 1
      })
    }
  }, [currentWordIndex])

  const playCurrentWordSound = useCallback(() => {
    if (currentWord.audioUrl) {
      const audio = new Audio(currentWord.audioUrl)
      audio.play().catch(console.error)
    }
  }, [currentWord])

  return (
    <DndProvider backend={HTML5Backend}>
      <div className="max-w-4xl mx-auto p-8">
        <div className="flex justify-between items-center mb-8">
          <h1 className="text-3xl font-bold text-purple-800">
            El Laboratorio de Ecos
          </h1>
          <div className="text-xl font-semibold text-purple-600">
            Puntos: {score} / {words.length}
          </div>
        </div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          key={currentWord.word}
        >
          <Card className="p-8 bg-white/90 backdrop-blur shadow-xl">
            <div className="relative h-64 mb-8 rounded-lg overflow-hidden">
              <Image
                src={currentWord.image}
                alt={currentWord.word}
                fill
                className="object-cover"
                sizes="(max-width: 768px) 100vw, 768px"
                priority
              />
            </div>

            <div className="text-center mb-8">
              <motion.div 
                key={currentWord.word}
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                className="flex flex-col items-center gap-4"
              >
                <h2 className="text-2xl font-bold">{currentWord.word}</h2>
                <Button
                  variant="outline"
                  onClick={playCurrentWordSound}
                  className="bg-purple-100 hover:bg-purple-200"
                >
                  Escuchar Sonido
                </Button>
                <p className="text-lg text-gray-600 mt-4">
                  ¿Qué letra hace el sonido inicial?
                </p>
              </motion.div>
            </div>

            <div className="flex flex-col items-center gap-8">
              <LetterDropZone
                onDrop={handleDrop}
                className="w-24 h-24 bg-gray-50"
              >
                {selectedLetter && (
                  <motion.span
                    initial={{ scale: 0 }}
                    animate={{ scale: 1 }}
                    className="text-3xl font-bold"
                  >
                    {selectedLetter}
                  </motion.span>
                )}
              </LetterDropZone>

              <div className="grid grid-cols-3 gap-4">
                {currentWord.options.map((letter) => (
                  <DraggableLetter
                    key={letter}
                    letter={letter}
                    isCorrect={selectedLetter === letter ? isCorrect : null}
                  />
                ))}
              </div>
            </div>
          </Card>
        </motion.div>

        <AnimatePresence mode="wait">
          {showCelebration && !isLevelComplete && (
            <CelebrationModal
              title="¡Excelente!"
              message="Has encontrado el sonido correcto"
              onClose={handleContinue}
            />
          )}

          {isLevelComplete && (
            <CelebrationModal
              title="¡Nivel Completado!"
              message={`¡Felicitaciones! Has completado el Laboratorio de Ecos con ${score} puntos.`}
              onClose={onBack}
              onNext={onComplete}
              isLevelComplete={true}
            />
          )}
        </AnimatePresence>

        <div className="mt-8 text-center">
          <Button
            onClick={onBack}
            className="bg-gray-500 hover:bg-gray-600 text-white"
          >
            Volver al Menú
          </Button>
        </div>
      </div>
    </DndProvider>
  )
}
