"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import Image from "next/image"
import Level1 from "@/components/game/Level1"
import Level2 from "@/components/game/Level2"
import Level3 from "@/components/game/Level3"
import { motion } from "framer-motion"

export default function Home() {
  const [currentLevel, setCurrentLevel] = useState<number | null>(null)

  const renderLevel = () => {
    switch (currentLevel) {
      case 1:
        return (
          <Level1
            onBack={() => setCurrentLevel(null)}
            onComplete={() => setCurrentLevel(2)}
          />
        )
      case 2:
        return (
          <Level2
            onBack={() => setCurrentLevel(null)}
            onComplete={() => setCurrentLevel(3)}
          />
        )
      case 3:
        return (
          <Level3
            onBack={() => setCurrentLevel(null)}
            onComplete={() => setCurrentLevel(null)}
          />
        )
      default:
        return (
          <div className="max-w-4xl mx-auto p-8">
            <h1 className="text-4xl font-bold text-center text-purple-800 mb-12">
              El Viaje de los Sonidos Mágicos
            </h1>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
              <motion.div
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                <Card 
                  className="p-6 cursor-pointer hover:shadow-lg transition-shadow"
                  onClick={() => setCurrentLevel(1)}
                >
                  <div className="relative h-48 mb-4 rounded-lg overflow-hidden">
                    <Image
                      src="https://images.pexels.com/photos/3905907/pexels-photo-3905907.jpeg"
                      alt="Laboratorio de Ecos"
                      fill
                      className="object-cover"
                      sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                      priority
                    />
                  </div>
                  <h2 className="text-2xl font-bold text-purple-800 mb-2">
                    Nivel 1
                  </h2>
                  <h3 className="text-xl text-purple-600 mb-2">
                    Laboratorio de Ecos
                  </h3>
                  <p className="text-gray-600">
                    Descubre los sonidos iniciales de las palabras
                  </p>
                </Card>
              </motion.div>

              <motion.div
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                <Card 
                  className="p-6 cursor-pointer hover:shadow-lg transition-shadow"
                  onClick={() => setCurrentLevel(2)}
                >
                  <div className="relative h-48 mb-4 rounded-lg overflow-hidden">
                    <Image
                      src="https://images.pexels.com/photos/4126743/pexels-photo-4126743.jpeg"
                      alt="Fábrica de Sílabas"
                      fill
                      className="object-cover"
                      sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                      priority
                    />
                  </div>
                  <h2 className="text-2xl font-bold text-purple-800 mb-2">
                    Nivel 2
                  </h2>
                  <h3 className="text-xl text-purple-600 mb-2">
                    Fábrica de Sílabas
                  </h3>
                  <p className="text-gray-600">
                    Une consonantes y vocales para formar sílabas
                  </p>
                </Card>
              </motion.div>

              <motion.div
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                <Card 
                  className="p-6 cursor-pointer hover:shadow-lg transition-shadow"
                  onClick={() => setCurrentLevel(3)}
                >
                  <div className="relative h-48 mb-4 rounded-lg overflow-hidden">
                    <Image
                      src="https://images.pexels.com/photos/4126741/pexels-photo-4126741.jpeg"
                      alt="Constructor de Palabras"
                      fill
                      className="object-cover"
                      sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                      priority
                    />
                  </div>
                  <h2 className="text-2xl font-bold text-purple-800 mb-2">
                    Nivel 3
                  </h2>
                  <h3 className="text-xl text-purple-600 mb-2">
                    Constructor de Palabras
                  </h3>
                  <p className="text-gray-600">
                    Forma palabras uniendo sílabas
                  </p>
                </Card>
              </motion.div>
            </div>
          </div>
        )
    }
  }

  return renderLevel()
}
