"use client"

import { useDrag } from 'react-dnd'
import { Button } from "@/components/ui/button"
import { useRef } from 'react'

interface DraggableSyllableProps {
  syllable: string
  isPlaced: boolean
}

export function DraggableSyllable({ syllable, isPlaced }: DraggableSyllableProps) {
  const ref = useRef<HTMLDivElement>(null)
  const [{ isDragging }, drag] = useDrag(() => ({
    type: 'syllable',
    item: { syllable },
    canDrag: !isPlaced,
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  }), [syllable, isPlaced])

  // Combine refs
  const dragRef = (element: HTMLDivElement | null) => {
    ref.current = element
    drag(element)
  }

  return (
    <div
      ref={dragRef}
      style={{
        opacity: isDragging ? 0.5 : isPlaced ? 0.3 : 1,
        cursor: isPlaced ? 'default' : 'move',
      }}
    >
      <Button
        className={`
          text-2xl font-bold h-16 min-w-[4rem] px-4
          ${isPlaced ? 'bg-gray-400' : 'bg-purple-500 hover:bg-purple-600'}
          transition-colors duration-200
          transform hover:scale-105
        `}
        disabled={isPlaced}
      >
        {syllable}
      </Button>
    </div>
  )
}
